						<!-- <span class="dt-square-btn" @click="handleAdd"><i class="el-icon-plus"></i></span> -->
						<dt-upload-btn 
							:beforeUpload="validateProjectName" 
							styleType="add-icon"
							action="/fileOperator/uploadFile"  
							@upload-success="handleUploadSuccess"></dt-upload-btn>
						<span class="dt-square-btn" 
							@click="handleDelete" 
							style="margin-left: 10px;">
							<i class="el-icon-minus"></i>
						</span>
