			</el-form>
			<!-- 填写说明弹窗 -->
				<dt-dialog key="3" class="large-dialog" :hasContent="true" :isShow="dialogShow" :hasBtn="false" :title="firstStepShow?'填写说明':'共同条款'">
			        <div class="dialog-content">
			        	<template v-if="firstStepShow">
				        	<p style="margin-bottom: 30px">目标任务书必须依据通过综合评审的项目申报书如实填写，不得随意变更内容。</p>
							<ul>
								<li>1.项目名称应以中国医学科学院医学与健康科技创新工程（以下简称创新工程）批复的名称为准。</li>
								<li>2. 承担单位为任务组织实施的责任单位，目标任务书由承担单位组织填写，编写人员应客观、真实地填报有关数据，尊重他人知识产权，遵守国家有关知识产权法规。</li>
								<li>3.对于伪造、篡改科学数据，抄袭他人著作、论文或者剽窃他人科研成果等科研不端行为，一经查实，将记入信用记录。</li>
								<li>4.按照给定格式编写，正文请用小4号仿宋字体、1.5倍行距，双面打印，简单装订；纸面不敷，可另加页。不得改变本任务书中表格格式和结构，带有…标记的表格可根据需要增加相应行数。</li>
								<li>5.任务须由一个法人单位进行针对性的申请。任务成员应为在编科技人员或博士后，不包括研究生和编制外聘用科技人员。</li>
								<li>6.专业应填目前所从事领域的专业，按二级学科专业填写，参照国家自然科学基金委的专业分类目录。</li>
								<li>7.任务负责人应承诺研究的真实性、实验材料和知识产权共享，并在所有上报的目标任务书中亲笔签字.</li>
							</ul>
							<h5 style="margin-top: 30px;">二、项目首席专家签字</h5>
							<p>项目首席专家应承诺研究的真实性、实验材料和知识产权共享，打印后在所有上报的目标任务书中亲笔签字。</p>
			        	</template>
			        	<template v-if="!firstStepShow">
			        		<p>（一）缔约各方的权利、义务</p>
			        		<p>第一条  缔约各方均应共同遵守《中华人民共和国合同法》、《中华人民共和国著作权法》、《中华人民共和国专利法》、《中华人民共和国科学技术进步法》及《中国医学科学院医学与健康科技创新工程项目管理办法》、《院外单位承担中国医学科学院医学与健康科技创新工程项目管理办法》、《中国医学科学院医学与健康科技创新工程项目经费管理办法》等法律和规定，严格遵守并认真履行本合同的各项条款，以及科研诚信的有关规定。</p>
			        		<p>（一）甲方</p>
		    				<p>甲方是中国医学科学院，应协调有关部门按合同约定提供研究经费。</p>
							<p>甲方有权监督、检查合同履行情况。合同履行期间，甲方根据需要有权委派专家组或科技评估、监理机构以及审计机构，对乙方和丙方履行本合同的情况进行检查、监督和审计。丙方完成项目研究任务后，由甲方负责进行项目验收。</p>
							<p>（二）乙方</p>
							<p>乙方是项目牵头单位，应按合同约定，为项目实施提供条件支撑和管理服务，对实施项目予以监督。</p>
							<p>乙方在签订本合同时，保证乙方具备承担项目资格所需的各项条件；如果乙方提供的相关信息不真实，并足以影响乙方承担项目资格的，本合同将视为无效合同，丙方承担由此产生的一切损失。</p>
							<p>（三）丙方</p>
