	    .el-form-item{ margin-bottom: 20px;}
	    .pro-no{
	    	margin-top: 30px;
	    	font-size: 20px;
			color: #B3B3B3;
			letter-spacing: -0.3px;
			line-height: 18px;
	    }
	    .sub-title{
	    	margin: 30px 0;
	    	font-size: 18px;
			color: #63B4FF;
			letter-spacing: -0.27px;
			line-height: 18px;
			>span{
				display: inline-block;
				position: relative;
				top: -2px;
				width: 4px;
				height: 17px;
				margin-right: 10px;
				vertical-align: middle;
				background-color: #63B4FF;
			}
	    }
	    .table-box{
	    	margin-left: 15px;
	    }
	    .sub-title-detail{
	    	margin-top: 30px;
	    	margin-bottom: 20px;
	    	font-size: 16px;
			color: #686868;
	    }

	    .el-select > .el-input,.el-select,.el-date-editor.el-input{
	    	width: 100%;
	    }
	    .year-tab{
	    	margin-left: 15px;
	    }
	    .all-fold-box{
	    	width: 100%;
	    	height: 40px;
	    	margin-top: 30px;
	    	line-height: 40px;
	    	text-align: center;
	    	background: rgba(99,180,255,0.20);
			border: 1px solid #63B4FF;
			border-radius: 7px;
			color: #63B4FF;
			font-size: 16px;
			cursor: pointer;
			.el-icon-arrow-up,.el-icon-arrow-down{
				margin-right: 10px;
				font-size: 18px;
				color: #63B4FF;
			}
	    }
	    .search-item{
	    	display: inline-block;
	    	// margin-bottom: 20px;
	    	margin-left: 15px;
	    	>span{
	    		display: inline-block;
	    		width: 130px;
	    		// padding-right: 6px;
	    		font-size: 16px;
	    		color: #696969;
	    		text-align: left;
	    	}
	    }
	    .tips{
	    	font-size: 16px;
			color: #B3B3B3;
			letter-spacing: -0.37px;
			line-height: 27px;
	    }
	    .tips.small{
	    	margin-top: 23px;
	    	font-size: 14px;
	    	h6{
				font-size: 16px;
				font-weight: bold;
			}
	    }
	    .el-table__row > td>.cell{ text-align: center; }
	    @media screen and (max-width: 1600px) {
			.dt-tab .dt-tab-item{
				font-size: 16px;
				padding: 9px 15px 12px;
			}
			.max-width-no.el-input{ max-width: 100%; }
			.max-width-no.el-select{ max-width: 100%; }
	    }
	    @media screen and (min-width: 1600px) {
			.el-input, .el-select,.el-date-editor.el-input{ max-width: 234px;}
			// .width-80{ width: 80%;}
			.width-90{ width: 90%;}
			.max-width-2{ max-width: 675px;}
			.max-width-1{ max-width: 432px;}
			.max-width-no.el-input{ max-width: 93%; }
			.el-select.max-width-no{ max-width: 93%; }
			.el-select.max-width-no .el-input{max-width: 100%;}
	    }
	  	.el-input__inner::-webkit-outer-spin-button,
		.el-input__inner::-webkit-inner-spin-button {
		    -webkit-appearance: none;
		}
		input.el-input__inner[type="number"] {
		    -moz-appearance: textfield;
		}
	}
</style>


